
.page {
  position: absolute;
  background-color: #fff ;
  width:100%;
}

.pageTransition-enter {
  opacity: 0;
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
}
.pageTransition-enter-active {
  z-index: 2;
  -webkit-transition: opacity 100ms ease-out, -webkit-transform 200ms ease-out;
  transition: opacity 100ms ease-out, -webkit-transform 200ms ease-out;
  -o-transition: opacity 100ms ease-out, transform 200ms ease-out;
  transition: opacity 100ms ease-out, transform 200ms ease-out;
  transition: opacity 100ms ease-out, transform 200ms ease-out, -webkit-transform 200ms ease-out;
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}
.pageTransition-exit-active {
  z-index: 1;
}




